.calendarWrapp {
    max-width: 767px;
    margin:0 auto;
}
.dropDownPPL {
    max-width: 767px;
    margin: 24px auto;
}
.fc {
    td.fc-sun,
    td.fc-sat {
        background-color: #eee;
    }
}


.fc-month-view,
.fc-agendaWeek-view .fc-day-grid,
.fc-agendaDay-view .fc-day-grid{
    .fc-event {
        padding: 6px 15px;
        display: inline-block;
        border-radius: 100px;
        color: rgb(255, 255, 255);
        font-size: 12px;
        text-align: center;
        margin-bottom: 2px;
    }
}



.fc-month-view,
.fc-agendaWeek-view,
.fc-agendaDay-view{
    .cat1 {
        background-color: rgb(255, 134, 139);
        box-shadow: 0 2px 4px rgba(255, 152, 152, 0.3);
        text-shadow: 0 1px 1px rgba(208, 2, 27, 0.46);
    }
    .cat2 {
        background-color: rgb(29, 140, 203);
        box-shadow: 0 2px 4px rgba(29, 140, 203, 0.22);
        text-shadow: 0 1px 1px #0F6494;
    }
    .cat3 {
        background-color: rgb(126, 211, 33);
        box-shadow: 0 2px 4px rgba(126, 211, 33, 0.22);
        text-shadow: 0 1px 1px #71C217;
    }
}

.cat1{
    .fc-event-dot {
         background-color: rgb(255, 134, 139);
    }
}
.cat2{
    .fc-event-dot {
        background-color: rgb(29, 140, 203);
    }
}
.cat3{
    .fc-event-dot {
        background-color: rgb(126, 211, 33);
    }
}

.fc-time-grid-container {
    .fc-title {
        transform: rotate(180deg);
        writing-mode: tb-rl;
        padding-bottom: 10px;
    }
}

    

.fc-row  {
    .fc-content-skeleton {
        td {
            border-color: inherit;
        }
    }
}