@import "lib/globals";
@import (less) "lib/normalize.css";

@import "lib/base";
@import "lib/functions";
@import "lib/dimensions";
@import "lib/typo";
@import "lib/helper";
@import "widgets/columns";

@import (less) "lib/fullcalendar.css";
@import "calendar";

body {
    font-family: 'Open sans', sans-serif;
}

main {
    max-width: 1024px;
    width: 100%;
    margin: 0 auto;
    box-shadow: 10px 10px 25px 3px rgba(0, 0, 0, 0.32);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    background-color: rgb(62,65,70);
    display: none;
}

.row {position: relative;}
.tr {
    width: 0;
    height: 0;
    border-left: 20px solid rgb(62, 65, 70);
    border-top: 20px solid rgba(0, 0, 0, 0);
    border-bottom: 20px solid rgba(0, 0, 0, 0);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateY(-50%);
    @media (max-width: 991px) {display: none;}
}

.leftSide {
    background-color: rgb(62,65,70);

}

.rightSide {
    background-color: rgb(56,59,64);

}

.imgWrap {
    border-radius: 50%;
    width:200px;
    height: 200px;
    margin: 0 auto;
    overflow: hidden;
}
.me {
    width:100%;
    position: relative;
    left: 0px;
}
.phWrapp {
    padding: 40px 0;
}
h1 {
    color: #fff;
    text-transform: uppercase;
    margin: 1em 0;
}
h2 {
    color: #ccc;
    text-transform: uppercase;
    font-size: 1.6em;
}
h3 {
    color: #fff;
    text-transform: uppercase;
    font-size: 1.2em;
}
.about,
.about2{
    width: 87%;
    margin: 40px auto;
    p {
        color: #fff;
        text-align: left;
        margin: 0 0 12px 0;
        line-height: 24px;
        a {
            color: orange;
            text-decoration: none;
            cursor: pointer;
        }
    }
}
.page1,.page2 {
    color: #fff;
    text-decoration: none;
    transition: 0.5s all;
    height: 30px;
    display: block;
    &:hover {
        font-size: 20px;
    }
}

.loader {
    position: absolute;
    top:0;
    bottom:0;
    right:0;
    left:0;
    text-align: center;
    background-color: #fff;
}

.loaderText {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}
.nextPage {
    color: #fff;
    text-decoration: none;
    float: right;
    margin-right: 40px;
    margin-bottom: 10px;
    cursor: pointer;
}

.popupWrap {
    position: absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background-color: rgb(62,65,70);
    display: none;
    z-index: 20;
}

.popupContent {
    max-width: 500px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    p {
        color: #fff;
    }
    .close {
        color: orange;
        text-decoration: none;
        cursor: pointer;
    }
}


@media (min-width: 991px) {
    .about,.about2 {min-height: 488px;}
}
@media (max-width: 991px) {
    .popupContent {
        position: static;
        padding: 20px 20px;
        transform: none;
        margin: 0 auto;
    }
}