@gap: 20px;

.row {
	clear: both;
	.clearFix();
	text-align: center;
	
	&.centered { display: inline-block; }
	
	&.three .column { width: (@pageWidth - @gap * 2) / 3; }
	&.four .column { width: (@pageWidth - @gap * 3) / 4; }

	.column {
		box-sizing: border-box;
		float: left;
		width: (100% - 5%) / 2;
		margin-bottom: 40px;
		margin-right: 5%;
		
		&.last { margin-right: 0 !important; }
		
		&.middle {
			float: none;
			margin-left: auto;
			margin-right: auto;
		}
		
		p:last-of-type { margin-bottom: 0; }
	}
}

.row:before, .row:after {
  content: "";
  display: table;
  clear: both; }

[class*="col-"] {
  float: left;
  padding: 5px;
  min-height: 1px;}

@media (min-width: 992px) {
    .l-col-3 {width: 30%;}
    .l-col-4 {width: 33%;}
    .l-col-9 {width: 70%;}
    .l-col-6 {width:50%;}
}



@media (max-width: 991px)  {
    .s-col-1 {width: 25%;}
    .s-col-2 {width: 50%;}
    .s-col-4 {width: 100%;}

}
